body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: SF-Pro-Rounded-Medium;
  overflow-x: hidden;
  letter-spacing: 0.25px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.center-x {
  display: flex;
  flex-direction: row;
